
















export default {
	data() {
		return {
			vertyText: "获取验证码",
			currentTime: 60,
			disabled: false,
			vertrycode:'',
			newphone:''
		}
	},
	methods: {
		update:function(){
			this.$utils.tRequest({
				url: this.$webUrl+'user_mobile_edit',
				method: 'POST',
				data: {
					code:this.vertrycode ,
					mobile:this.newphone
				},
				success: res => {
					uni.showModal({
						title: '提示',
						content: res.msg,
						showCancel: false,
						cancelText: '',
						confirmText: '知道了',
						success: res => {},
						fail: () => {},
						complete: () => {}
					});
				},
				fail: () => {},
				complete: () => {}
			})
		},
		getCode: function() {
			var that = this;
			this.$utils.tRequest({
				url: this.$webUrl + "user_verify_smscode",
				data: {
					mobile: this.phone
				},
				success: res => {
					that.AppSendVerifyCode(res);
				},
				fail: () => {
			
				},
				complete: () => {
			
				}
			})
		},
		AppSendVerifyCode(res) {
			console.log(JSON.stringify(res));
			var that = this;
			if (res.code == 0) {
				var currentTime = that.currentTime;
				this.interval = setInterval(function() {
					this.disabled = true
					currentTime--;
					that.vertyText = currentTime + '秒'
					if (currentTime <= 0) {
						clearInterval(this.interval)
						that.vertyText = '重新发送',
							that.currentTime = 60,
							that.disabled = false
					}
		
				}, 1000)
			} else {
				uni.showModal({
					title: '提示',
					content: res.msg,
					showCancel: false,
					cancelText: '',
					confirmText: '知道了',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
		},
	},
	
	computed: {
		phone(){
			return this.$store.getters.getUserInfo.mobile
		}
	}

}
